nav {
  background: $nav-background;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 3.75rem;

  .nav-container {
    width: 66rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    list-style: none;
    height: 100%;

    img {
      vertical-align: sub;
    }
    h2 {
      font-size: 2.25rem;

      span {
        font-weight: 400;
      }
    }

    a {
      margin: 3rem;
      margin-left: 0;
      text-decoration: none;

      &:nth-child(5) {
        margin: 0;
        margin-left: auto;
      }
    }
  }
}
